<template>
    <el-dialog :title="title" :visible.sync="dialogVisible" width="50%" :before-close="cancel">
        <div class="row-list">
            <span class="name">第三方订单编号：</span>
            <el-input v-model="data.orderNum" :disabled="true"/>
        </div>
        <div class="row-list">
            <span class="name">收货人姓名：</span>
            <el-input v-model="data.userName" :disabled="true"/>
        </div>
        <div class="row-list">
            <span class="name">收货人手机号：</span>
            <el-input v-model="data.mobile"/>
        </div>
        <div class="row-list">
            <span class="name">收货人地址：</span>
            <el-input v-model="data.address" :disabled="true"/>
        </div>
        <div class="row-list">
            <span class="name">收货人地址：</span>
            <el-select class="select" v-model="province" placeholder="省" @change="provinceHandle">
                <el-option
                    v-for="(item, index) in provinceList"
                    :key="index"
                    :label="item"
                    :value="index"
                ></el-option>
            </el-select>
            <el-select class="select" v-model="city" placeholder="市" @change="cityHandle">
                <el-option
                    v-for="(item, index) in cityList[cityIndex]"
                    :key="index"
                    :label="item"
                    :value="index"
                ></el-option>
            </el-select>
            <el-select class="select" v-model="area" placeholder="区">
                <el-option
                    v-for="(item, index) in areaList[cityIndex][areaIndex]"
                    :key="index"
                    :label="item"
                    :value="index"
                ></el-option>
            </el-select>
        </div>
        <div class="row-list">
            <span class="name">详细地址：</span>
            <el-input type="text" placeholder="详细地址" v-model="detailAddress"/>
        </div>
        <!-- <div class="row-list">
            <span class="name">商品名：</span>
            <el-input v-model="data.excelGoodsName" :disabled="true"/>
        </div>
        <div class="row-list">
            <span class="name">买家账号：</span>
            <el-input v-model="data.buyAccount" :disabled="true"/>
        </div>-->
        <span slot="footer" class="dialog-footer">
            <el-button @click="cancel">取 消</el-button>
            <el-button type="primary" @click="confirm">确 定</el-button>
        </span>
    </el-dialog>
</template>

<script>
import { provinceList, cityList, areaList } from "../utils/city_data.js";
export default {
  props: ["data", "title"],
  data() {
    return {
      dialogVisible: true,
      province: "",
      city: "",
      area: "",
      provinceList,
      cityList,
      areaList,
      cityIndex: 0,
      areaIndex: 0,
      detailAddress: ""
    };
  },
  methods: {
    cancel() {
      this.$emit("cancel");
    },
    confirm() {
      const mobileLength = this.data.mobile.length;
      if (mobileLength !== 11) {
        this.$message("请输入11位的手机号");
        return;
      }
      const province = this.province && this.provinceList[this.province];
      const city =
        this.province && this.city && this.cityList[this.province][this.city];
      const area =
        this.province &&
        this.city &&
        this.area &&
        this.areaList[this.province][this.city][this.area];
      const isAddressSelect = province && city && area;
      if (!isAddressSelect) {
        this.$message("请选择省市区");
        return;
      }

      if (!this.detailAddress) {
        this.$message("请填写地址详情");
        return;
      }
      this.data.address = province + city + area + this.detailAddress;
      this.$emit("confirm", this.data);
    },
    provinceHandle(val) {
      this.cityIndex = val;
      this.city = "";
      this.area = "";
    },
    cityHandle(val) {
      this.areaIndex = val;
      this.area = "";
    }
  }
};
</script>

<style lang="less" scoped>
.row-list {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  .name {
    width: 150px;
  }
  &:last-child {
    margin: 0;
  }
}
.select {
  width: 33%;
}
</style>



